<template>
  <div>
    <div class="btn">
      <el-button type="primary" @click="addGrade">添加年级</el-button>
    </div>

    <el-dialog title="添加年级" :visible.sync="dialogGrade" width="600px" :close-on-click-modal="false">
      <div class="box">
        <el-form :model="form" label-width="80px">
          <el-form-item label="年级">
            <el-input v-model="form.title" placeholder="例:一年级"></el-input>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newGrade">提交</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-table :data="tableData" ref="multipleTable">
      <el-table-column prop="id" label="年级ID" align="center"></el-table-column>
      <el-table-column prop="title" label="年级名称" align="center"></el-table-column>
      <el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      :visible.sync="dialogDel"
      title="删除年级"
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;">是否删除该年级</div>
      <span>
        <el-button type="primary" @click="toDel">删除</el-button>
        <el-button type="danger" @click="dialogDel = false">取消</el-button>
      </span>
    </el-dialog>

    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageSize"
        layout="sizes, prev, pager, next, jumper"
        :total="totalPage"
        @size-change="handleSizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/index.js";

export default {
  name: "gradems",
  data() {
    return {
      dialogGrade: false,
      form: {
        title: ""
      },
      tableData: [],
      classList: [],
      dialogDel: false,
      grade_id: "",
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,

      permissions: localStorage.getItem("permissions"),
      role: localStorage.getItem("role")
    };
  },
  mounted() {
    this.getGrade();
  },
  methods: {
    // 获取年级列表
    getGrade() {
      var self = this;
      API.grades(self.currentPage).then(res => {
        self.tableData = res.data;
        self.totalPage = res.total;
      });
    },
    // 增加年级
    addGrade() {
      var self = this;
      self.dialogGrade = true;
    },
    newGrade() {
      var self = this;
      API.grade(self.form).then(res => {
        self.$message.success("提交成功");
        self.dialogGrade = false;
        self.getGrade();
        self.form = {};
        self.currentPage = 1;
        window.location.reload();
      });
    },
    // 操作
    handleDel(index, row) {
      var self = this;
      self.dialogDel = true;
      self.grade_id = row.id;
    },
    toDel() {
      var self = this;
      API.delGrade(self.grade_id)
        .then(res => {
          self.$message.success("删除成功");
          self.dialogDel = false;
          self.getGrade();
          self.currentPage = 1;
        })
        .catch(err => {});
    },

    // 分页
    handleCurrentChange(val) {
      var self = this;
      API.grades(val, self.pageSize).then(res => {
        self.tableData = res.data;
        self.totalPage = res.total;
      });
    },
    // 每页多少条
    handleSizeChange(val) {
      var self = this;
      self.pageSize = val;
      API.grades(1, self.pageSize).then(res => {
        self.tableData = res.data;
        self.totalPage = res.total;
        self.currentPage = 1;
      });
    }
  }
};
</script>

